:root {
  --main-font-family: "Arial", sans-serif;
  --min-width: 320px;
  --max-width-container: 1300px;
  --main-white: #fff;
  --main-yellow: #FFD262;
  --dark-yellow: #F7BA4B;
  --input-yellow: #FDC949;
  --dark-color: #191919;
  --main-purple: #8836AE;
  --main-blue: #1573FF;
  --dark-blue: #0f5aca;
  --light-gray: #BDBDBD;
  --main-gray: #979797;
  --main-pink: #FF8562;
  --main-green: #05F800;
  --dark-green: #39cc36;
  --bg-color: #1E0044;
}

* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

a:focus-visible,
button:focus-visible {
  outline: 2px solid var(--main-blue);
}

a {
  color: inherit;
  outline: none;
}

aside,
nav,
footer,
header,
section {
  display: block;
}

html,
body {
  height: 100%;
  min-width: var(--min-width);
}

body {
  font-size: 16px;
  line-height: 1;
  font-family: var(--main-font-family);
  color: var(--main-white);
  background-color: var(--bg-color);
  font-weight: 400;
  font-style: normal;
}

input,
button,
textarea {
  font-size: inherit;
  font-family: var(--main-font-family);
}

button {
  cursor: pointer;
  background-color: transparent;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-size: inherit;
}

.wrapper {
  width: 100%;
  min-height: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  background: url("../img/general/body-bg.jpg") center/cover no-repeat;
}

.main {
  flex: 1 1 auto;
}
.main__hero {
  padding: 159px 0 60px 0;
}
@media (max-width: 480px) {
  .main__hero {
    padding: 122px 0 30px 0;
  }
}
.main__about, .main__why, .main__who, .main__news, .main__organizer, .main__partners {
  padding: 80px 0;
}
@media (max-width: 992px) {
  .main__about, .main__why, .main__who, .main__news, .main__organizer, .main__partners {
    padding: 60px 0;
  }
}
@media (max-width: 480px) {
  .main__about, .main__why, .main__who, .main__news, .main__organizer, .main__partners {
    padding: 40px 0;
  }
}
.main__registration {
  padding: 80px 0 160px 0;
}
@media (max-width: 992px) {
  .main__registration {
    padding: 60px 0 120px 0;
  }
}
@media (max-width: 480px) {
  .main__registration {
    padding: 40px 0 80px 0;
  }
}

.container {
  max-width: var(--max-width-container);
  padding: 0 15px;
  margin: 0 auto;
}
@media (max-width: 480px) {
  .container {
    padding: 0 10px;
  }
}

.logo {
  display: flex;
  align-items: center;
}
.logo img {
  width: 75px;
  height: 68px;
  margin-right: 10px;
}
@media (max-width: 480px) {
