  }
}

.text-descr {
  font-size: 22px;
  font-weight: 700;
  line-height: 150%;
}
@media (max-width: 480px) {
  .text-descr {
    font-size: 16px;
  }
}

.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  color: var(--dark-color);
  background-color: var(--main-yellow);
  font-weight: 700;
  transition: background-color 0.3s ease;
}
@media (any-hover: hover) {
  .btn:hover {
    background-color: var(--dark-yellow);
  }
}

.btn-content {
  font-size: 20px;
  padding: 20px 50px;
  border-radius: 30px;
}
@media (max-width: 480px) {
