}

.hidden {
  overflow: hidden;
}

.label {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-size: 18px;
  padding: 15px 26px;
  border-radius: 30px;
  border: 1px solid var(--main-white);
  margin-bottom: 25px;
}
@media (max-width: 768px) {
