    font-size: 16px;
    padding: 12px 20px;
  }
}
@media (max-width: 480px) {
  .label {
    margin-bottom: 15px;
  }
}

.title {
  font-size: 48px;
  font-weight: 700;
  letter-spacing: -1.5px;
}
@media (max-width: 768px) {
  .title {
    font-size: 42px;
  }
}
@media (max-width: 480px) {
  .title {
    font-size: 32px;
  }
}

.title-small {
  font-size: 32px;
  font-weight: 700;
  line-height: 110%;
}
@media (max-width: 480px) {
  .title-small {
    font-size: 22px;
  }
}

.top {
  display: flex;
  flex-direction: column;
  align-items: center;
  border-bottom: 1px solid var(--main-gray);
  padding-bottom: 25px;
  margin-bottom: 50px;
}
@media (max-width: 480px) {
  .top {
    margin-bottom: 20px;
    padding-bottom: 10px;
  }
}

.pics {
  display: flex;
  align-items: center;
}
@media (max-width: 992px) {
  .pics {
    justify-content: center;
  }
}
.pics__img {
  flex: 0 0 159px;
  height: 159px;
  border-radius: 100%;
  border: 3px solid var(--main-white);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  margin-right: -30px;
}
@media (max-width: 1300px) {
  .pics__img {
    flex: 0 0 12vw;
    height: 12vw;
  }
}
@media (max-width: 768px) {
  .pics__img {
    flex: 0 0 18vw;
    height: 18vw;
  }
}
@media (max-width: 480px) {
  .pics__img {
    flex: 0 0 80px;
    height: 80px;
  }
}

@font-face {
  font-family: "Arial";
  src: url("../fonts/Arial-Regular.woff2") format("woff2"), url("../Arial-Regular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Arial";
  src: url("../fonts/Arial-Bold.woff2") format("woff2"), url("../Arial-Bold.woff") format("woff");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Arial";
  src: url("../fonts/Arial-Black.woff2") format("woff2"), url("../Arial-Black.woff") format("woff");
  font-weight: 900;
  font-display: swap;
  font-style: normal;
}
.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  min-height: 109px;
  background-color: rgba(18, 1, 39, 0.6);
  z-index: 99;
}
.header.active {
  box-shadow: 0 0 40px rgba(255, 255, 255, 0.8);
  background-color: rgba(18, 1, 39, 0.9);
}
@media (max-width: 480px) {
  .header {
    min-height: 82px;
  }
}
.header__body {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 0;
}
@media (max-width: 480px) {
  .header__body {
    padding: 10px 0;
  }
}
.header__logo {
  margin-right: 15px;
  z-index: 101;
}
.header__menu {
  margin-right: 15px;
}
@media (max-width: 992px) {
  .header__menu {
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    transform: translateY(-110%);
    width: 100%;
    height: 100%;
    overflow-y: auto;
    z-index: 100;
    background-color: var(--bg-color);
    transition: transform 0.4s ease, opacity 0.4s ease, visibility 0.4s ease;
    padding: 32px 16px;
  }
  .header__menu.active {
    transform: translateY(0);
    transition: transform 0.4s ease, opacity 0.4s ease, visibility 0.4s ease;
  }
}
.header__list {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}
@media (max-width: 992px) {
  .header__list {
    flex-direction: column;
    justify-content: center;
    flex-wrap: no-wrap;
    margin-bottom: 80px;
  }
}
.header__item:not(.header__item:last-child) {
  margin-right: 45px;
}
@media (max-width: 1200px) {
  .header__item:not(.header__item:last-child) {
    margin-right: 30px;
  }
}
@media (max-width: 992px) {
  .header__item:not(.header__item:last-child) {
    margin: 0 0 40px 0;
  }
}
.header__link {
  font-size: 22px;
  font-weight: 700;
  transition: color 0.3s ease;
}
@media (any-hover: hover) {
  .header__link:hover {
    color: var(--dark-yellow);
  }
}
@media (max-width: 1200px) {
  .header__link {
    font-size: 20px;
  }
}
@media (max-width: 992px) {
  .header__link {
    font-size: 5vh;
  }
}
.header__btns {
  display: flex;
  align-items: center;
  z-index: 101;
}
.header__actions {
  display: flex;
  justify-content: center;
}
.header__register {
  font-size: 22px;
  border-radius: 10px;
  padding: 14px 40px;
}
@media (max-width: 992px) {
  .header__register {
    margin-right: 30px;
  }
}
@media (max-width: 768px) {
  .header__register {
    font-size: 4vh;
    margin-right: 0;
  }
}
.header__burger {
  display: none;
}
@media (max-width: 992px) {
  .header__burger {
    display: block;
    position: relative;
    width: 40px;
    height: 24px;
    z-index: 11;
    transition: background-color 0.3s ease 0s;
  }
  .header__burger span {
    transition: all 0.3s ease 0s;
    top: calc(50% - 1px);
    left: 0;
    position: absolute;
    width: 100%;
    height: 3px;
    background-color: var(--main-white);
  }
  .header__burger span:first-child {
    top: 0;
  }
  .header__burger span:last-child {
    top: auto;
    bottom: 0;
  }
  .header__burger.active span {
    transform: scale(0);
  }
  .header__burger.active span:first-child {
    transform: rotate(-45deg);
    top: calc(50% - 2px);
  }
  .header__burger.active span:last-child {
    transform: rotate(45deg);
    bottom: calc(50% - 1px);
  }
}
@media (any-hover: hover) {
  .header__burger:hover span {
    background-color: var(--dark-yellow);
  }
}
@media (max-width: 480px) {
  .header__burger {
    width: 30px;
    height: 20px;
  }
}

